@import "../_furnishing-variables.less";
@import "../_furnishing-mixins.less";

.fn-@{cms-ns}-furnishing-volume{
	background: @body-background;

	.volume-hd{
		width: 240px;
		background: @furnishing-color;
		padding: 5px 0;
		overflow: hidden;
		border-radius: 0 45px 45px 0;
		vertical-align: middle;

		.name{
			margin:5px 15px 0 10px;
			font-size: @font-size-lg;
			color: @furnishing-deep-color;
			float: left;
		}
		.time{
			font-size: @font-size-base;
			color: @body-background;
			.lh{
				color:@ht-color;
				font-size: @font-size-xxl;
				padding:0 2px;
				font-weight: 400;
			}
		}

	}
	.item{
		padding:10px 0;
		overflow: hidden;
		.img{
			.size(160px);
			float: left;
		}
		.detail{
			margin-left:170px;
			margin-right:10px;
			text-align: center;

			.red-tlt{
				font-size: @font-size-lg;
				.inline-block();
				padding:0 5px;
				background: @ht-color;
				color: @body-background;
				.ellipsis();
				margin-bottom:3px;
			}
			.bl-tlt{
				font-size: @font-size-sm;
				overflow: hidden;
			}
			.tlt{
				margin-top:15px;
				font-size: @font-size-base;
				.ellipsis(2);
				overflow: hidden;
				text-align: left;
			}
			.price{
				margin:3px 0;
				text-align: left;
				

				overflow: hidden;
				font-size: @font-size-base;
				.now{
					margin-right:3px;
				}
				.old{
					color:#999;
					font-size: @font-size-sm;
				}
			}
			.bottom{
				overflow: hidden;
				font-size: @font-size-sm;
				.btn{
					padding: 5px;
					color: @body-background;
					background: @ht-color;
					border-radius: 5px;
					text-align: center;
					float: left;
				}
				.num{
					
					text-align: right;
					font-size: @font-size-sm;
					color:#999;
					span{
						color:#3c3c3c;
					}
				}
			}
		}
	}
}