@import "../incl/com-item.less";
@import "../_furnishing-variables.less";
@import "../_furnishing-mixins.less";

.fn-@{cms-ns}-furnishing-timetab{
	.tabs{
		width:100%;
		display: table;

		li{
			padding:6px 0;
			display: table-cell;
			color:#999;
			background: #2d3845;

			p{
				
				line-height: 1.3;
				.ellipsis();
				text-align: center;
				font-size: @font-size-base;
			}

			&.item-active{
				background: @furnishing-color;
				color: @body-background;
				position: relative;

				&:after{
					content:'';
					display: block;
					border-style:solid dashed dashed dashed;
					border-color: @furnishing-color transparent transparent transparent;
					border-width: 6px;
					.absolute(_,_,-12px,50%);
					margin-left:-3px;

				}
			}
		}

		&.col5{
			li{
				width:20%;

				&.item-active{
					width:20%;
				}
			}
		}
	}
	.times{
		background: #ffe7c1;
		border-bottom:1px #ffd28a solid;
		color:#6a4100;
		padding:12px 10px;
		overflow: hidden;

		p{
			float: left;
			.ellipsis();
		}
		&-hd{
			width:40%;
		}
		&-limit{
			text-align: right;
			width:60%;
			.word{
				padding-right:5px;
			}
			.num{
				background: #000;
				color: @body-background;
				padding:2px;
				border-radius: 3px;
			}
		}
	}

	.list .com-item{
		background: @body-background;
		height:125px;

		.intro{
			border-top:1px solid #ccc;
		}

		&:first-child{
			.intro{
				border-top:0 none;
			}
		}
	}
}