@import "../../_plugin/cmp-item-list.less";


@import "../_furnishing-variables.less";
@import "../_furnishing-mixins.less";

.fn-@{cms-ns}-furnishing-prolist{

	.item-v-list{
		.cmp-item-list();
	}

	.item-v-list{
		.item{
		    overflow: visible;

			&-complex{
				.complex{
					width:50%;
					float: left;

					&-wrap{
						.absolute(0,_,_,0);
						.size(100%);
					}
					.inner-img{
						width:80%;
						margin:8% auto 5%;
						position: relative;
						&:before{
							display: block;
							padding-top:100%;
							content:'';
						}
						.inner-img100{
							.absolute(0,_,_,0);
							.size(100%);
							border-radius: 100%;
						}
					}
					.c-text{
						text-align: center;
						font-size: @font-size-lg;
						line-height: 1.6;
						.ellipsis();
					}
					.pro{
						color:#666;
					}
					.active{
						color:@ht-color;
					}
					&:before{
						padding-top:100%;
						padding-bottom:40px !important;
					}
				}
				.img-box{
					&:before{
						padding-top:100%;
						padding-bottom:84px;
					}
				}
			}
		}

		.time{
			padding:0 5px;
			background: @ht-color;
			color:#fff;
			.absolute(-3px,0,_,_);
			z-index:2;

			&:before{
				content:'';
				display: block;
				border-style:dashed dashed solid;
				border-color:transparent transparent #a00619;
				border-width: 2px;
				.absolute(1px,_,_,-2px);
				z-index:1;
				transform:rotate(135deg);
			}
		}
	}
}
