@import "../_furnishing-variables.less";
@import "../_furnishing-mixins.less";

.com-item{
	display: block;
	width:100%;
	height:110px;
	.img{
		float: left;
		.size(110px);
	}
	.intro{
		position: relative;
		height:100%;
		padding-top:10px;
		margin-left:120px;
		padding-right:10px;

		.tlt{
			line-height: 18px;
			height: 36px;
			color:#333;
			overflow: hidden;
		}
		.price{
			.absolute(_,_,10px,0);

			.old{
				color:#999;
				font-size:12px;

				del{
					text-decoration: line-through;
				}
			}
			.now{
				font-size:15px;
				color:@ht-color;
			}
			.mon{
				padding-left:3px;
			}
		}
		.btn{
			.absolute(_,10px,10px,_);
			width:80px;
			border:1px solid @ht-color;
			background-color: @ht-color;
			border-radius: 5px;
			text-align: center;
			overflow: hidden;
			display: table-cell;

			&-up{
				color:#333;
				line-height: 18px;
				background-color: #fff;
				font-size:10px;
			}
			&-down{
				color: #fff;
				line-height: 26px;
				background-color:@ht-color;
			}

			&.o-line{
				border:0 none;
				.btn-down{
					height: 26px;
					line-height: 26px;
				}

				.no-sale{
					background-color:#868686;
				}
			}
		}
	}
}
