@import "../_furnishing-variables.less";
@import "../_furnishing-mixins.less";

.fn-@{cms-ns}-furnishing-picgrid{
	.swiper-container{
		background: @body-background;
		padding-bottom: 10px;
	}

	.swiper-item{
		.img-box{
			display: block;

			&:before{
				padding-top: 40%;
			}
		}

		.bt{
			.absolute(_,_,0,0);
			background: rgba(0,0,0,.8);
			.size(100%, 34px);
			line-height: 34px;
			color: @body-background;

			.text{
				float: left;
				padding-left: 10px;
			}

			.price{
				float: right;
				padding-right: 10px;

				.num{
					font-size: @font-size-xl;
					padding-right: 5px;
					font-family: 'arial';
					color: @ht-color;
				}
			}
		}
	}

	.pic-box{
		width:50%;
		border-bottom:1px @body-background solid;

		&:before{
			content:'';
			display: block;
			padding-top:71%;
		}

		&:nth-child(odd):after{
			content:'';
			.absolute(0,0,_,_);
			.size(1px,100%);
			background: @body-background;
		}
	}
}